export const cycles = () => {
    //Cycles

    //–ü–æ–¥—Å—á–∏—Ç–∞—Ç—å —Å—É–º–º—É –≤—Å–µ—Ö —á–∏—Å–µ–ª –≤ –∑–∞–¥–∞–Ω–Ω–æ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –¥–∏–∞–ø–∞–∑–æ–Ω–µ.
    document.getElementById(
        "calculateAmount"
    ).onclick = function calculateAmount() {
        let rangeStart = +prompt("–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤–æ–µ —á–∏—Å–ª–æ –¥–∏–∞–ø–∞–∑–æ–Ω–∞");
        let rangeEnd = +prompt("–í–≤–µ–¥–∏—Ç–µ –ø–æ—Å–ª–µ–¥–Ω–µ–µ —á–∏—Å–ª–æ –¥–∏–∞–ø–∞–∑–æ–Ω–∞");
        let sum = 0;
        while (rangeStart <= rangeEnd) {
            sum += rangeStart;
            rangeStart++;
        }
        alert(`–°—É–º–º–∞ –≤—Å–µ—Ö —á–∏—Å–µ–ª –¥–∏–∞–ø–∞–∑–æ–Ω–∞ —Ä–∞–≤–Ω–∞ ${sum}`);
    };

    //–ó–∞–ø—Ä–æ—Å–∏—Ç—å 2 —á–∏—Å–ª–∞ –∏ –Ω–∞–π—Ç–∏ —Ç–æ–ª—å–∫–æ –Ω–∞–∏–±–æ–ª—å—à–∏–π –æ–±—â–∏–π –¥–µ–ª–∏—Ç–µ–ª—å.
    document.getElementById("commonDivisor").onclick = function commonDivisor() {
        const firstNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤–æ–µ —á–∏—Å–ª–æ");
        const secondNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–µ —á–∏—Å–ª–æ");
        if (firstNumber == 0);
        alert(secondNumber);
        while (secondNumber != 0) {
            if (firstNumber > secondNumber) firstNumber = firstNumber - secondNumber;
            else secondNumber = secondNumber - firstNumber;
        }
        alert(`–ù–∞–∏–±–æ–ª—å—à–∏–π –æ–±—â–∏–π –¥–µ–ª–∏—Ç–µ–ª—å ${firstNumber}`);
    };

    //–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —á–∏—Å–ª–æ –∏ –≤—ã–≤–µ—Å—Ç–∏ –≤—Å–µ –¥–µ–ª–∏—Ç–µ–ª–∏ —ç—Ç–æ–≥–æ —á–∏—Å–ª–∞.
    document.getElementById("allDivisors").onclick = function allDivisors() {
        const insertNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ");
        let i = 0;
        while (i++ <= insertNumber) {
            if (insertNumber % i == 0) alert(`–î–µ–ª–∏—Ç–µ–ª–∏ —ç—Ç–æ–≥–æ —á–∏—Å–ª–∞ ${i}`);
        }
    };

    //–û–ø—Ä–µ–¥–µ–ª–∏—Ç—å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏—Ñ—Ä –≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–º —á–∏—Å–ª–µ.
    document.getElementById("numberDigits").onclick = function numberDigits() {
        const yourNumber = prompt("–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ", "");
        let length = yourNumber.length;
        alert(`–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏—Ñ—Ä –≤ –≤–≤–µ–¥–µ–Ω–Ω–æ–º —á–∏—Å–ª–µ ${length}`);
    };

    //–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 10 —á–∏—Å–µ–ª –∏ –ø–æ–¥—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ –æ–Ω –≤–≤–µ–ª –ø–æ–ª–æ–∂–∏—Ç–µ–ª—å–Ω—ã—Ö, –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã—Ö –∏ –Ω—É–ª–µ–π. –ü—Ä–∏ —ç—Ç–æ–º —Ç–∞–∫–∂–µ –ø–æ—Å—á–∏—Ç–∞—Ç—å, —Å–∫–æ–ª—å–∫–æ —á–µ—Ç–Ω—ã—Ö –∏ –Ω–µ—á–µ—Ç–Ω—ã—Ö.
    document.getElementById("mathTricks").onclick = function mathTrickss() {
        let zero = 0;
        let posNumber = 0;
        let negNumber = 0;
        let evenNumber = 0;
        let oddNumber = 0;
        for (let i = 1; i <= 10; i++) {
            let yourNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ: ");
            if (yourNumber > 0) {
                posNumber += 1;
            } else if (yourNumber < 0) {
                negNumber++;
            } else if (yourNumber == 0) {
                zero += 1;
            }
            if (yourNumber % 2 == 0) {
                evenNumber += 1;
            } else {
                oddNumber += 1;
            }
        }
        alert(
            `–ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –Ω—É–ª–µ–π: ${zero} 
        –ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –ø–æ–ª–æ–∂–∏—Ç–µ–ª—å–Ω—ã—Ö —á–∏—Å–µ–ª: ${posNumber} 
        –ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –æ—Ç—Ä–∏—Ü–∞—Ç–µ–ª—å–Ω—ã—Ö —á–∏—Å–µ–ª: ${negNumber} 
        –ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö —á–µ—Ç–Ω—ã—Ö —á–∏—Å–µ–ª: ${evenNumber} 
        –ö–æ–ª–ª–∏—á–µ—Å—Ç–≤–æ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –Ω–µ—á–µ—Ç–Ω—ã—Ö —á–∏—Å–µ–ª: ${oddNumber}`
        );
    };

    //–ó–∞—Ü–∏–∫–ª–∏—Ç—å –∫–∞–ª—å–∫—É–ª—è—Ç–æ—Ä. –ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è 2 —á–∏—Å–ª–∞ –∏ –∑–Ω–∞–∫, —Ä–µ—à–∏—Ç—å –ø—Ä–∏–º–µ—Ä, –≤—ã–≤–µ—Å—Ç–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç –∏ —Å–ø—Ä–æ—Å–∏—Ç—å, —Ö–æ—á–µ—Ç –ª–∏ –æ–Ω —Ä–µ—à–∏—Ç—å –µ—â–µ –æ–¥–∏–Ω –ø—Ä–∏–º–µ—Ä. <br> –ò —Ç–∞–∫ –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –æ—Ç–∫–∞–∂–µ—Ç—Å—è.
    document.getElementById(
        "LoopCalculator"
    ).onclick = function LoopCalculator() {
        while (true) {
            const firstNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ –ø–µ—Ä–≤–æ–µ —á–∏—Å–ª–æ", "");
            const arithmeticOperation = prompt(
                "–í–≤–µ–¥–∏—Ç–µ –∞—Ä–∏—Ñ–º–µ—Ç–∏—á–µ—Å–∫–æ–µ –¥–µ–π—Å—Ç–≤–∏–µ: +,-,*,/",
                ""
            );
            const secondNumber = +prompt("–í–≤–µ–¥–∏—Ç–µ –≤—Ç–æ—Ä–æ–µ —á–∏—Å–ª–æ", "");
            if (arithmeticOperation == "+") {
                result = firstNumber + secondNumber;
            } else if (arithmeticOperation == "-") {
                result = firstNumber - secondNumber;
            } else if (arithmeticOperation == "*") {
                result = firstNumber * secondNumber;
            } else if (arithmeticOperation == "/") {
                result = firstNumber / secondNumber;
            }
            alert(firstNumber + arithmeticOperation + secondNumber + " = " + result);
            if (!confirm("–•–æ—Ç–∏—Ç–µ –ª–∏ –≤—ã —Ä–µ—à–∏—Ç—å –µ—â–µ –æ–¥–∏–Ω –ø—Ä–∏–º–µ—Ä?")) break;
        }
    };

    //–ó–∞–ø—Ä–æ—Å–∏—Ç—å —É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —á–∏—Å–ª–æ –∏ –Ω–∞ —Å–∫–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä –µ–≥–æ —Å–¥–≤–∏–Ω—É—Ç—å. –°–¥–≤–∏–Ω—É—Ç—å —Ü–∏—Ñ—Ä—ã —á–∏—Å–ª–∞ –∏ –≤—ã–≤–µ—Å—Ç–∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç (–µ—Å–ª–∏ —á–∏—Å–ª–æ 123456 —Å–¥–≤–∏–Ω—É—Ç—å –Ω–∞ 2 —Ü–∏—Ñ—Ä—ã, —Ç–æ –ø–æ–ª—É—á–∏—Ç—Å—è 345612).
    document.getElementById("shiftNumbers").onclick = function shiftNumbers() {
        let nubmer = prompt("–í–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ", "");
        let move = +prompt("–ù–∞ —Å–∫–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä –Ω—É–∂–Ω–æ —Å–¥–≤–∏–Ω—É—Ç—å –≤–≤–µ–¥–µ–Ω–Ω–æ–µ —á–∏—Å–ª–æ", "");

        alert(nubmer.slice(move).concat(nubmer.slice(0, move)));
    };

    //–ó–∞—Ü–∏–∫–ª–∏—Ç—å –≤—ã–≤–æ–¥ –¥–Ω–µ–π –Ω–µ–¥–µ–ª–∏ —Ç–∞–∫–∏–º –æ–±—Ä–∞–∑–æ–º: ¬´–î–µ–Ω—å –Ω–µ–¥–µ–ª–∏. –•–æ—Ç–∏—Ç–µ —É–≤–∏–¥–µ—Ç—å —Å–ª–µ–¥—É—é—â–∏–π –¥–µ–Ω—å?¬ª –∏ —Ç–∞–∫ –¥–æ —Ç–µ—Ö –ø–æ—Ä, –ø–æ–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∞–∂–∏–º–∞–µ—Ç OK.
    document.getElementById("LoopDays").onclick = function LoopDays() {
        const days = [
            "–ü–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫",
            "–í—Ç–æ—Ä–Ω–∏–∫",
            "–°—Ä–µ–¥–∞",
            "–ß–µ—Ç–≤–µ—Ä–≥",
            "–ü—è—Ç–Ω–∏—Ü–∞",
            "–°—É–±–±–æ—Ç–∞",
            "–í–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ",
        ];
        let currDay = 0;

        while (confirm(`${days[currDay]}. –•–æ—Ç–∏—Ç–µ —É–≤–∏–¥–µ—Ç—å —Å–ª–µ–¥—É—é—â–∏–π –¥–µ–Ω—å?`)) {
            currDay = (currDay + 1) % days.length;
        }
    };

    //–í—ã–≤–µ—Å—Ç–∏ —Ç–∞–±–ª–∏—Ü—É —É–º–Ω–æ–∂–µ–Ω–∏—è –¥–ª—è –≤—Å–µ—Ö —á–∏—Å–µ–ª –æ—Ç 2 –¥–æ 9. –ö–∞–∂–¥–æ–µ —á–∏—Å–ª–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ —É–º–Ω–æ–∂–∏—Ç—å –Ω–∞ —á–∏—Å–ª–∞ –æ—Ç 1 –¥–æ 10.
    document.getElementById(
        "multiplicationTable"
    ).onclick = function multiplicationTable() {
        do {
            let a = 1;
            let b = 9;
            let check = b - a;
            if (check >= 0) {
                for (let c = a; c <= b; c++) {
                    for (let d = 1; d <= 10; d++) {
                        document.write("<br>" + c + " x " + d + " = " + c * d);
                    }
                    document.write("<p>");
                }
            } else {
                alert("First number is higher than the second, PLease try again.");
            }
        } while (check < 0);
    };

    //–ò–≥—Ä–∞ ¬´–£–≥–∞–¥–∞–π —á–∏—Å–ª–æ¬ª. –ü—Ä–µ–¥–ª–æ–∂–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é –∑–∞–≥–∞–¥–∞—Ç—å —á–∏—Å–ª–æ –æ—Ç 0 –¥–æ 100 –∏ –æ—Ç–≥–∞–¥–∞—Ç—å –µ–≥–æ
    document.getElementById("GuessNumber").onclick = function GuessNumber() {
        alert("–ó–∞–≥–∞–¥–∞–π—Ç–µ —á–∏—Å–ª–æ –æ—Ç 1 –¥–æ 100");
        let minNumber = 0;
        let maxNumber = 101;
        do {
            let N = Math.round(minNumber + (maxNumber - minNumber) / 2);
            let guessNumber = prompt(`–í–∞—à–µ —á–∏—Å–ª–æ >, < –∏–ª–∏ = ${N}`);
            if (guessNumber == "<") {
                maxNumber = N - 1;
            } else if (guessNumber == ">") {
                minNumber = N + 1;
            } else if (guessNumber == "=") {
                alert(`–ò–≥—Ä–∞ –æ–∫–æ–Ω—á–µ–Ω–∞. –í—ã –∑–∞–≥–∞–¥–∞–ª–∏ —á–∏—Å–ª–æ ${N}`);
                break;
            }
        } while (minNumber !== false);
    };
};